.sidebarContainer {
    background: #363740; 
    height: 100vh; 
}
.appLogoContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 35px 25px;
    width: 67%;
}
.appBarItems {
    display: flex;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
    padding: 17px 0px 17px 25px;
    font-weight: 400;
    letter-spacing: 0.5px;
    color: #9FA2B4;
    text-decoration: none;
    border-left: 4px solid transparent;
}
.activeAppBarItems {
    display: flex;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
    padding: 17px 0px 17px 25px;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-decoration: none;
    background-color: rgba(157, 162, 180, 0.25);
    color: #DDE2FF;
    border-left: 4px solid;
}
.appBarItems:hover {
    background-color: rgba(157, 162, 180, 0.25);
    transition: .3s;
    color: #DDE2FF;
}

 