/* constant styles */

.flexRowSpace {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flexRow {
  display: flex;
  align-items: center;
}


/* shared styles */

.slideBarContainer {
  background: rgb(222, 226, 234);
  border-radius: 25px;
  align-items: center;
  display: flex;
}
.slideBar {
  height: 6px;
  background-color: var(--naksh-black);
  border-radius: 15px;
}
.underline {
  height: 0.1px;
  background: #DFE0EB;
  opacity: 0.2;
}
.dropdown {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
  padding: 10px;
  box-shadow: 0 1px 1px rgb(0 0 0 / 10%), 0 2px 3px rgb(0 0 0 / 9%);
  cursor: pointer;
}
.containerCard {
  background:white;
  border:1px solid #DFE0EB;
  border-radius:7px;
  padding: 25px;
}
.label {
  font-weight: 600;
}
.selectInput {
  height:40;
  color:rgba(0, 25, 76, 0.5);
}
