:root {
  --naksh-blue: #0052FF;
  --naksh-red: #E62F2F;
  --naksh-black: #00194C;
  --naksh-light-gray:  #F7F8FC; 
  --naksh-disabled-gray: #BFC5D2;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: 'Inter', sans-serif; */
