.emptyPlaceholder {
    text-align: center;
    margin-top: 10%;
    font-weight: 600;
    font-size: 22px;
}
.artformStyle {
    background: white;
    padding: 13px;
    border-radius: 10px;
    box-shadow: 0 1px 1px rgb(0 0 0 / 5%), 0 2px 3px rgb(0 0 0 / 9%);
    cursor: pointer;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}