.tabHeading {
    font-weight:600;
    cursor:pointer;
}
.searchIcon {
    position: absolute;
    z-index: 1;
    top: 7px;
    left: 10px;
}
.artistCard {
    background-color: white;
    height: 250px;
    border-radius: 10px;
    box-shadow: 0 1px 1px rgb(0 0 0 / 5%), 0 2px 3px rgb(0 0 0 / 9%);
    position: relative;
    margin-bottom: 20px;
    cursor: pointer;
}
.bottomHeading {
    background: white;
    position: absolute;
    bottom: 0;
    width: 100%;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    padding: 10px 15px;
}
.phoneNum {
    position: absolute;
    top: 40px;
    left: 25px;
    font-weight: 600;
    border-right: 1px solid rgba(0, 25, 76, 0.5);
    padding-right: 10px;
}
.editIcon {
    height: 40px;
    background: white;
    position: absolute;
    top: 5px;
    right: 5px;
    width: 40px;
    border-radius: 40px;
    align-items: center;
    justify-content: center;
    display: flex;
}
.subHeadings {
    margin-top: 15px;
    font-weight: 600;
    margin-bottom: 2px;
    font-size: 18px;
}