body {
  background-color: var(--naksh-light-gray) !important;
  color: var(--naksh-black) !important;
}
.appContainer {
  display: flex;
}
.colOne {
  width: 250px;
  background: #363740;
}
.colTwo {
  width: 94%;
  padding: 35px 25px;
  height: 100vh;
  overflow-y: scroll;
}
.colTwo::-webkit-scrollbar {
  width: 5px;
}
 
.colTwo::-webkit-scrollbar-thumb {
  background-color: var(--naksh-black);
  outline: 1px solid slategrey;
}
